<template>
	<div>
		<div class="www">	
		<div class="bigbig">
			<load v-if="istrue"></load>
			<bscroll class="scrollleft" ref='city_ooo' v-else>
				<div>
					<div class="bigList">
						<div class="city_hot">
							<h2 class="hot">热门城市</h2>
							<ul class="cityhot_list">
								<li v-for="item in hotList" :key="item.id" @tap="hander(item.nm , item.id)">
									{{item.nm}}
								</li>
							</ul>
						</div>
						<div class="cities" ref="city_sort">
							<ul v-for="item in cityList" :key = "item.nm">
								<h2 class="forlist">
									{{item.index}}
								</h2>
								<li v-for="items in item.list" class="nmlist" @tap="hander(items.nm , items.id)">
									{{items.nm}}
								</li>
							</ul>
						</div>
					</div>
				</div>
			</bscroll>
		</div>	
		<div class="right">
			<ul class="rightList">
				<!--touchstart手机端的触摸按钮-->
				<li v-for="(item,index) in cityList" :key="item.index" @touchstart="hanhan(index)" class="rll">
					{{item.index}}
				</li>
			</ul>
		</div>
	</div>	
	</div>
</template>
<script>
	export default{
		name:"redir",
		data(){
			return{
				cityList:[],
				hotList:[],
				istrue:true
			}
		},
		mounted(){
			/*从本地存储中取数据*/
			let cityList = window.localStorage.getItem('cityList');
			let hotList = window.localStorage.getItem('hotList');
			if(cityList && hotList){
				/*这说明本地有数据*/
				/*将字符全的数据转为对象*/
				this.cityList = JSON.parse(cityList);
				this.hotList =JSON.parse(hotList);
				this.istrue = false;
			}else{
				this.axios.get('http://39.97.33.178/api/cityList').then((res) =>{
					const msg = res.data.msg;
					if (msg == 'ok') {
						this.istrue = false;
						const cities = res.data.data.cities;
						//接受两个从formatCityList里传出来的数组
						const {cityList , hotList} = this.formatCityList(cities);
						this.cityList = cityList;
						this.hotList = hotList;
						/*本地存储,利用html5的特性,从本地存储中获取已经用过的数据能优化，提高性能*/
						/*存储citylist跟hotlist的数据，将其转为字符串*/
						window.localStorage.setItem('cityList' , JSON.stringify(cityList));
						window.localStorage.setItem('hotList' , JSON.stringify(hotList));
					}
				});
			}
		},
		methods:{
			formatCityList(cities){
				let cityList = [];
				let hotList = [];
				for(let i=0;i<cities.length;i++){
					//截取每个城市拼音的第一个字母并把他变成大写
					const firstLetter = cities[i].py.substring(0,1).toUpperCase();

					if(toCom(firstLetter)){
						//cityList没有这个就push进去
					cityList.push( { index:firstLetter,list:[{nm:cities[i].nm , id:cities[i].id}] } );
					}else{
						//第一个字母有相同的就判断是不是有id跟其一样的，没有就将其push进去
						for(let j=0;j<cityList.length;j++ ){
							if (cityList[j].index === firstLetter) {

								/*这个别人写的,下面个自己写的cityList[j].list.push({nm:cities[i].nm , id:cities[i].id});*/
								if(cityList[j].list.id !== cities[i].id){
									cityList[j].list.push({nm:cities[i].nm , id: cities[i].id});
								}
							}
						}
					}	

					if (cities[i].isHot == 1) {
						hotList.push({nm:cities[i].nm , id:cities[i].id})
					}
				}

				//数组排序 sort() js排序
				cityList.sort((n1,n2) => {
					if(n1.index < n2.index){
						return -1;
					}else if(n1.index > n2.index){
						return 1;
					}else{
						return 0;
					}
				});
			
				//鉴定cityList里是否有这个字母
				function toCom(firstLetter){
					for(let i=0;i<cityList.length;i++){
						if(cityList[i].index === firstLetter){
							return false;
						}
					}
					return true;
				}

				return{
					cityList,
					hotList
				};
			},
			hanhan(index){
				/*先通过ref获取到元素，然后在把他的位置传给ref的父元素*/
				const h2 = this.$refs.city_sort.getElementsByTagName('h2');
				/*用了better-scroll后这个不适用了*/
				/*this.$refs.city_sort.parentNode.scrollTop = h2[index].offsetTop;*/
				/*better-scroll的用法*/
				this.$refs.city_ooo.toScrollTop(-h2[index].offsetTop);
			},
			hander(nm , id){
				/*因为当前状态管理store不是异步的，所以可以通过commit来修改其数据*/
				this.$store.commit('city/CITY_INFO',{nm,id});
				/*通过路由方法跳转*/
				this.$router.push('/sousuo/');
				window.localStorage.setItem('nowNM',nm);
				window.localStorage.setItem('nowID',id);
			}
		}
	}
</script>
<style scoped>
	*{
		padding: 0;
		margin: 0;
	}
    ul li {
    	list-style-type: none;
    }
    .www{display: flex;}
    .scrollleft{
    	width: 100%;
    }
    .bigbig{
    	display: flex;
    	width: 95%;
    	height: calc(100vh - 140px);
    	overflow: hidden;
    }

 	.right{
 		position: absolute;
 		right: 0;
 		height: calc(100vh - 140px);
 		width:5%;
 		font-size: 14px;
 		text-align: center;
 	}
 	.rightList{
  		width: 100%;
  		height: 100%;
  		flex: 1;
        display:flex;
        flex-flow: column;
 	}
 	.rll{
 		flex: 1;
 	}
    .bigList{ 		
    	background: #f8f8f8;
    	width: 100%;
    	height: 100%;
    	margin-right: 12px;
    }
    .hot{
    	background: #eee;
    	color: black;
    	font-size: 16px;
    }
    .nmlist{
    	margin-bottom: 5px;
    	width: 100%;
    }
    .forlist{
    	background: #eee;
    	font-size: 14px;
    	margin-top: 10px;
    	margin-bottom: 10px;
    }
	.cityhot_list{
		display: flex;
		color: black;
		width: 100%;
		text-align: center;
		align-items: center;
		flex-wrap: wrap;
		margin-top: 20px;
		margin-bottom: 20px;
	}
	.cityhot_list li{
		width: 30%;
		line-height: 25px;
		font-size: 14px;
		background: pink;
		margin-left: 2%;
		margin-top: 5px;
		border-radius: 5px;
	}
</style>


